import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Button, Tag, Divider } from 'antd';
import LineCharts from './LineCharts';
import AreaCharts from './AreaCharts';
import PieCharts from './PieCharts';
import GroupColumn from './GroupColumn'
import './index.less';

@inject('ZCloudDeviceStore')
@observer
class ZDeviceMonitor extends Component {
	state = {
		data1: [],
		data2: [],
		data3: [],
		data4: [],dataValue:[]
	};
	render() {
		const { monitorList } = this.props.ZCloudDeviceStore;
		return (
			<div style={{ background: '#fff',paddingBottom:'10px',height:'100%',width:'100%' }}>
				<ul className='title-all-device'>
					{monitorList.map(item => {
						return (
							<li key={item.devId}>
								<div className='image'>
									<img
										alt='图片'
										src='https://assets-boranet.oss-cn-hangzhou.aliyuncs.com/applet/device01.jpg'
										width='100%'
										height='100%'
									/>
								</div>
								<div className='content-div'>
									<div style={{ position: 'relative' }}>
										<h4>{item.devId}</h4>
										<div style={{ position: 'absolute', top: 0, right: 0 }}>
											{item.devStatus == 2 ? (
												<Tag color='#f50'>告警</Tag>
											) : item.devStatus == 1 ? (
												<Tag color='#2db7f5'>运行</Tag>
											) : item.devStatus == 0 ? (
												<Tag color='#B3B3B3'>离线</Tag>
											) : (
												<Tag color='#FFBF00'>待机</Tag>
											)}
										</div>
										<ul>
											<li>
												<span>设备编号：</span>
												<span>{item.devId}</span>
											</li>
											<li>
												<span>上传时间：</span>
												<span>{item.updateTime}</span>
											</li>
										</ul>
									</div>
								</div>
							</li>
						);
					})}
				</ul>
				<ul className='device-second1'>
					<li style={{ width: '50%' }}>
						<h3>设备转速</h3>
						{/* 近1小时 */}
						<LineCharts data={this.state.data1} />
					</li>
					<li style={{ width: '50%' }}>
						<h3>设备利用率</h3>
						<GroupColumn fields={this.state.dataValue} data={this.state.data2} />
					</li>
				</ul>
				<ul className='device-second1'>
					<li>
						<h3>设备OEE</h3>
						<AreaCharts data={this.state.data4} />
					</li>
					<li>
						<h3>设备倍率</h3>
						<LineCharts data={this.state.data3} />
					</li>
				</ul>
			</div>
		);
	}
	componentWillUnmount() {
		this.timer = null;
	}
	componentDidMount() {
		this.props.ZCloudDeviceStore.getMonitor()
		// this.timer = setInterval(()=>{
		// 	this.props.ZCloudDeviceStore.getMonitor()
		// },10000)
		/* 设置所有列表的动态数据 */
		let data1 = [],
			data2 = [],
			data3 = [],
			data4 = [];
		/* 利用率 */
		let dataValue = []
		for (let key = -1; key > -6; key--) {
			dataValue.push(`${this.getDay(key)}`);
		}
		for (let idx = 1; idx < 7; idx++) {
			let item = {
				index:idx,
				name: 'OP' + (idx) + '0',
				[dataValue[0]]: this.getRandomIntInclusive(50, 80),
				[dataValue[1]]: this.getRandomIntInclusive(50, 80),
				[dataValue[2]]: this.getRandomIntInclusive(50, 80),
				[dataValue[3]]: this.getRandomIntInclusive(50, 80),
				[dataValue[4]]: this.getRandomIntInclusive(50, 80),
			};
			data2.push(item);
		}
		// data2 = data2.reverse();
		/* 设备转速 */
		for (let i = 1; i < 7; i++) {
			let list = [];
			for (let key = 60; key >= 0; key -= 10) {
				let item = {
					month: this.getNowFormatDate(key),
					city: 'OP' + i + '0',
					temperature: this.getRandomIntInclusive(30, 100)
				};
				list.push(item);
			}
			data1.push(...list);
		}

		/* 设备倍率 */
		for (let i = 1; i < 7; i++) {
			let list = [];
			for (let key = 60; key >= 0; key -= 10) {
				let item = {
					month: this.getNowFormatDate(key),
					city: 'OP' + i + '0',
					temperature: this.getRandomIntInclusive(30, 100)
				};
				list.push(item);
			}

			data3.push(...list);
			
		}

		/* 设备OEE */
		for (let i = 1; i < 7; i++) {
			let list = [];
			for (let key = -1; key > -6; key--) {
				let item = {
					country: 'OP' + i + '0',
					year: this.getDay(key),
					value: this.getRandomIntInclusive(50, 80)
				};
				list.push(item);
			}
			data4.push(...list);
		}
		data4 = data4.reverse();
		this.setState({
			data1,
			data2,
			data3,
			data4,
			dataValue
		});
	}
	getNowFormatDate = index => {
		let date = new Date();
		let seperator2 = ':';
		//前十分钟时间
		let interTimes = parseInt(index) * 60 * 1000;

		interTimes = parseInt(interTimes);
		date = new Date(Date.parse(date) - interTimes);
		let hour = date.getHours(),minutes = date.getMinutes(),seconds = date.getSeconds();

		if (hour >= 0 && hour <= 9) {
			hour = '0' + hour;
		}
		if (minutes >= 0 && minutes <= 9) {
			minutes = '0' + minutes;
		}
		if (seconds >= 0 && seconds <= 9) {
			seconds = '0' + seconds;
		}
		let currentdate = hour + seperator2 + minutes + seperator2 + seconds;
		return currentdate;
	};
	getDay = day => {
		let today = new Date();
		let targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
		today.setTime(targetday_milliseconds); //注意，这行是关键代码
		let tYear = today.getFullYear();
		let tMonth = today.getMonth();
		let tDate = today.getDate();
		const doHandleMonth = month => {
			let m = month;
			if (month.toString().length == 1) {
				m = '0' + month;
			}
			return m;
		};
		tMonth = doHandleMonth(tMonth + 1);
		tDate = doHandleMonth(tDate);
		return tYear + '' + tMonth + '' + tDate;
	};
	getRandomIntInclusive = (min, max) => {
		min = Math.ceil(min);
		max = Math.floor(max);
		return Math.floor(Math.random() * (max - min + 1)) + min; //含最大值，含最小值
	};
}
export default ZDeviceMonitor;
